@import "./../../../variables";

.whole-paid-analysis-with-map {

  #percent {
    border-radius: 3px;
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
    color: #a30236;
    font-size: 14px;
    line-height: 1.5em;
    margin-top: 4px;
    font-family: "ALS Hauss", sans-serif;
    border: 1px solid white;
  }

  .recalculate {
    font-family: "ALS Hauss", sans-serif;
    font-size: 14px;
    color: white;
    line-height: 1.5em;
    border: 1px solid white;
    background: #a30236;

    border-radius: 3px;
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
  }

  .data-table {
    width: 100%;
    border-collapse: collapse;
    line-height: 1.5em;
    color: #252525;

    th {
      border: 1px solid #ffffff;
      font-weight: 500;
      background: #a30236;
      color: white;
      padding-left: 4px;
    }

    .data-tr {
      font-size: 14px;
      cursor: pointer;

      &.yellow {
        background: rgba(245, 192, 54, 0.4);
      }

      &.lightgreen {
        background: rgba(163, 202, 52, 0.4);
      }

      &.red {
        background: rgba(219, 114, 49, 0.4);
      }

      td {
        padding-left: 4px;
      }
    }
  }

  .map-icon {
    position: absolute;
    top: 0;
    right: 0;
    display: block;
    width: 30px;
    cursor: pointer;
  }

}