.map-backhack {
  font-family: "ALS Hauss", sans-serif;

  width: 100%;
  position: relative;
  padding-bottom: 55%; /* ratio 16/9 */

  &.--invisible {
    display: none;
  }

  .map-wrapper {
    border: none;
    position: absolute;
    width: 100%;
    height: 100%;
    overflow: auto;

    .map {
      display: block;
    }
  }

  .zoom {
    position: absolute;
    bottom: 11px;

    .zoom_b {
      display: block;
      width: 30px;
      height: 30px;
      color: #A30236;
      border: 1px solid #E4E4E4;
      background: #FAFAFA;
      box-sizing: border-box;
      border-radius: 2px;
      font-size: 25px;

      &.plus {
        border-bottom: none;
        border-bottom-right-radius: 0;
        border-bottom-left-radius: 0;
      }

      &.minus {
        border-top: none;
        border-top-left-radius: 0;
        border-top-right-radius: 0;
      }
    }

    .separator {
      width: 30px;
      height: 1px;
      //noinspection CssInvalidFunction
      background: radial-gradient(#e4e4e4 0% 50%, #FAFAFA 50% 100%);
      box-sizing: border-box;
      border-left: 1px solid #e4e4e4;
      border-right: 1px solid #e4e4e4;
    }
  }
}