@import "./../../variables";

.filial-analysis {
  display: flex;
  flex: 1 1 0;
  flex-direction: column;
  text-align: left;
  position: relative;

  .header {
    font-size: 31px;
    font-weight: 500;
  }

  .selects-container {
    padding-bottom: 40px;
  }

  .lets-analyse {
    background: #A30236;
    color: white;
    padding: 11px 20px 8px;
    border-radius: 10px;
    font-size: 14px;
    font-weight: 500;
    border: none;
    margin-right: auto;
    cursor: pointer;

    &:disabled {
      background: #9d9d9d;
    }
  }

  .tags {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }

  .tag-wrapper {
    display: flex;
    align-items: center;
    font-size: 14px;
    margin-right: 30px;
    margin-bottom: 10px;

    &:last-of-type {
      margin-right: 0;
    }

    .hashtag {
      font-size: 18px;
      font-weight: 500;
      color: #A30236;
      margin-right: 8px;
    }
  }

  .region-data {
    display: flex;

    .block {
      border-radius: 10px;
      padding: 26px;

      &.recommendations {
        background: #FAFAFA;
        font-size: 16px;
        margin-right: 30px;
        .header {
          font-size: 16px;
          font-weight: 600;
          color: #A30236;
          margin-bottom: 6px;
        }

        .slide-header {
          font-size: 24px;
          font-weight: 500;
          margin-bottom: 30px;
        }

        .slidecontainer {
          margin-bottom: 30px;
        }
      }

      &.region {
        background: #A30236;
        font-size: 14px;
        color: rgba(255, 255, 255, 0.7);
        height: fit-content;

        .region-name {
          font-size: 16px;
          font-weight: 600;
          color: white;
          margin-bottom: 10px;
        }


      }


    }

    & > * {
      flex: 1 1 0;
    }
  }

  .--invisible {
    display: none;
  }
}