@import "./../../variables";

.simulator {
  flex: 1 1 auto;
  display: flex;
  flex-direction: column;

  .button-wrapper {
    width: 100%;
    display: flex;
  }

  .menu-button {
    margin: 0;
    padding-inline: 18px;
  }

  .top {
    margin-bottom: 9px;
  }

  .bottom {
    margin-top: 9px;
  }

  .left {
    margin-right: 9px;
  }

  .right {
    margin-left: 9px;
  }


}