@import "./../../variables";

.start-page {
  background: url("./../../sources/svg/startPageBg.svg") fixed;
  min-height: 100%;
  background-size: cover;


  .top-part {
    height: 80px;
    border-bottom: 1px solid #A0A0A0;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    justify-content: space-between;

    .logo {
      margin-left: 44px;
      height: 42px;
    }

    .contacts-block {
      display: flex;
      text-align: left;

      .type {
        font-size: 12px;
        color: #A0A0A0;
      }

      .data {
        font-size: 16px;
        color: #A30236;
      }

      .contact-wrapper {
        margin-right: 34px;
      }
    }
  }





  .bottom-part {
    display: flex;
    flex-direction: column;
    .headers {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-top: 22px;
      margin-left: 44px;

      .links {
        display: flex;
        align-items: center;

        .nav {
          margin-right: 30px;
          color: #252525;
          cursor: pointer;

          &:hover {
            color: #A30236;

          }
        }
      }

      .enter {
        color: #A30236;
        margin-right: 50px;
        cursor: pointer;
        position: relative;

        &:before {
          display: block;
          width: 100%;
          height: 3px;
          content: "";
          background: #a30236;
          position: absolute;
          bottom: -8px;
        }
      }
    }

    .title-and-button {
      margin-top: 170px;
      margin-bottom: 170px;
      margin-left: 44px;
      margin-right: auto;
      display: flex;
      align-items: center;

      .title {
        font-size: 18px;
        width: 540px;
        font-weight: 500;
        margin-right: 74px;
        text-align: left;
      }

      .button {
        text-decoration: none;
        color: white;
        font-size: 12px;
        display: block;
        background: linear-gradient(95.69deg, #A30236 3.42%, #D90348 93.2%);
        border-radius: 8px;
        padding: 17px 50px 14px;
      }
    }

    .schema1 {
      margin-left: 44px;
      width: 990px;
    }

    .schema2 {
      margin-top: 90px;
      margin-left: 44px;
      margin-right: 20px;
      margin-bottom: 100px;
      max-width: 1371px;
    }
  }

}